export default{
	props:{
		goods:{
			type:Object,
			default(){
				return {}
			}
		}
	},
	data(){
		return{
			
		}
	},
	methods:{
		goodsInfo(){
			uni.navigateTo({
				url:'/pages/details/details?id='+this.goods.bargainId
			})
		}
	},
	filters:{
		getImgUrl(val){
			return JSON.parse(val)[0].url
		},
		filterTime(val){
			let d= new Date(val)
			return (d.getMonth() + 1)+'月'+d.getDay()+'日'
		}
	}
}
